<template>
  <div class="container d-flex">
    <div class="col-md-5">scsc</div>
    <div class="col-md-7">
      <h2>{{ product.productName }}</h2>
      <div v-for="(item, key) in product.subProduct" :key="key">
        <!-- {{item.image}} -->
        {{item.attributeValue}}
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  components: {},

  data() {
    return {
      product: {},
      subProduct: {},
      category: {},
      sortBy: "id",
      sort: "desc",
      pageSize: 12,
      pageNumber: 1,
      totalPages: 1,
      totalElements: 0,

      // priceProducts: [],

      // listType: [],
      // url: this.urlbe + "/product/list?",
      // search: "",
      // gender: "",
      // categorytype: 0,
      // type: "",
      // userRateFilter: 0,
      // priceGT: 0,
      // priceLT: 0,
      // typeSort: "id",
      // isSort: "desc",
      // shoppingCart: [],
      // displayFilter: false,
      // displayTypeFilter: false,
    };
  },

  props: ["baseUrl"],

  methods: {
    async getProduct() {
      try {
        const response = await axios.get(
          this.baseUrl + "/product/product/" + this.$route.params.id
        );
        this.product = response.data;
        return response.data;
      } catch (error) {
        console.error(error);
      }
    },

    async getCategory() {
      try {
        const response = await axios.get(
          this.baseUrl + "/product/category/" + this.$route.params.id
        );
        this.category = response.data;
        return response.data;
      } catch (error) {
        console.error(error);
      }
    },

    async getListProduct() {
      try {
        const response = await axios.get(
          this.baseUrl +
            "/product/product?category=" +
            this.$route.params.id +
            "&size=" +
            this.pageSize +
            "&page=" +
            (this.pageNumber - 1) +
            "&sort=" +
            this.sortBy +
            "," +
            this.sort
        );
        this.products = response.data.content;
        this.totalElements = response.data.totalElements;
        this.totalPages = response.data.totalPages;
        console.log(this.products[1].subProduct[0].image[0].imgURL);
        return response.data;
      } catch (error) {
        console.error(error);
      }
    },

    changeSortBy(sortId) {
      this.sortBy = sortId;
      this.pageNumber = 1;
      this.getListProduct();
    },

    changeSort() {
      if (this.sort == "desc") {
        this.sort = "asc";
      } else if (this.sort == "asc") {
        this.sort = "desc";
      }
      this.getListProduct();
    },

    async changePageSize(size) {
      this.pageSize = size;
      this.pageNumber = 1;
      this.getListProduct();
    },

    async changePage(page) {
      console.log(this.pageNumber);

      if (page == "prev" && this.pageNumber > 1) {
        this.pageNumber--;
      } else if (page == "after" && this.pageNumber < this.totalPages) {
        this.pageNumber++;
      } else if (typeof page == "number") {
        this.pageNumber = page;
      }
      this.getListProduct();
    },
  },

  async created() {
    this.getProduct();
    // this.getListProduct();
    // this.getCategory();
  },

  computed: {
    pages() {
      let pages = [];
      for (let i = 1; i <= this.totalPages; i++) {
        if (
          i == 1 ||
          i == this.totalPages ||
          Math.abs(i - this.pageNumber) <= 2
        ) {
          pages.push(i);
        } else if (Math.abs(i - this.pageNumber) == 3) {
          pages.push("...");
        }
      }
      return pages;
    },

    priceProducts() {
      let priceProducts = [];
      for (let i = 0; i < this.products.length; i++) {
        let list = [];
        for (let j = 0; j < this.products[i].subProduct.length; j++) {
          list.push(this.products[i].subProduct[j].price);
        }
        priceProducts.push({
          min: Math.min(...list),
          max: Math.max(...list),
        });
      }
      return priceProducts;
    },

    imagesProduct() {
      let pages = [];
      for (let i = 1; i <= this.totalPages; i++) {
        if (
          i == 1 ||
          i == this.totalPages ||
          Math.abs(i - this.pageNumber) <= 2
        ) {
          pages.push(i);
        } else if (Math.abs(i - this.pageNumber) == 3) {
          pages.push("...");
        }
      }
      return pages;
    },
  },

  watch: {
    async $route() {
      this.getListProduct();
      this.getCategory();
    },
  },
};
</script>
<style scoped>
.btn-sort-by {
  margin: 10px;
  border: 1px solid #000;
}

.btn-sort-by:hover {
  border: 1px solid #42b983;
  color: #42b983;
}

.btn-sort-by-active {
  background-color: #42b983;
  border: 1px solid #42b983;
  color: #fff;
}

.btn-sort-by-active:hover {
  border: 1px solid #42b983;
  color: #fff;
}

.btn-sort {
  cursor: pointer;
  font-size: 25px;
}

.sort-active {
  color: #42b983;
}

.btn-pagination:hover {
  border: 1px solid #42b983;
  color: #42b983;
}

.btn-pagination-active {
  background-color: #42b983;
  border: 1px solid #42b983;
  color: #fff;
}

.btn-pagination-active:hover {
  background-color: #42b983;
  border: 1px solid #42b983;
  color: #fff;
}

.product-item {
  position: relative;
}

.product-item:hover {
  box-shadow: 0 5px 10px #888888;
  cursor: pointer;
  transform: translateY(-1px);
}

.img-product {
  height: 250px;
}

.img-product img {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 250px;
  width: auto;
}

@media (max-width: 739px) {
  .btn-sort-by {
    margin: 5px;
    border: 1px solid #000;
  }

  .img-product {
    height: 200px;
  }

  .img-product img {
    height: 200px;
  }
}
</style>